import React from "react";
import { Row, Col, Select } from "antd";
import img from "./css/images/Vector.svg";
const { Option } = Select;
export default function MyProfile() {
  function handleChange(value) {
    console.log(`selected ${value}`);
  }
  return (
    <div>
      <div className="top" style={{ marginTop: "40px" }}>
        <Row gutter={16}>
          <Col span={7} style={{ textAlign: "center" }}>
            <img src={img} alt="" />
            <div
              className="info"
              style={{
                textAlign: "left",
                marginTop: "40px",
                marginLeft: "130px",
              }}
            >
              <div className="df">Date of birth: </div>
              <div className="df">Sex: </div>
              <div className="df">Address Company: </div>
              <div className="df">Address Home: </div>
            </div>
          </Col>
          <Col span={17}>
            <h1
              className="name"
              style={{ fontSize: "48px", marginBottom: "40px" }}
            >
              ADMIN
            </h1>
            <div className="mail" style={{ fontSize: "36px" }}>
              Email: admin@gmail.com
            </div>

            <div
              className="info"
              style={{
                textAlign: "left",
                marginTop: "70px",
              }}
            >
              <div className="df date">
                <form>
                  <label></label>
                  <input
                    type="date"
                    id="birthday"
                    name="birthday"
                    style={{ borderWidth: "0 0 1px 0" }}
                    value="2018-07-22"
                  />
                </form>
              </div>
              <div className="df sex">
                <Select
                  defaultValue="Male"
                  style={{ width: 120 }}
                  onChange={handleChange}
                >
                  <Option value="Male">Male</Option>
                  <Option value="Female">Female</Option>
                </Select>
              </div>
              <div className="df" style={{ textDecoration: "underline" }}>
                15, Duy Tan, Dich Vong Hau, Cau Giay, Ha Noi
              </div>
              <div className="df" style={{ textDecoration: "underline" }}>
                15, Duy Tan, Dich Vong Hau, Cau Giay, Ha Noi
              </div>
            </div>
          </Col>
        </Row>
      </div>
    </div>
  );
}
