import React from "react";

import { Col, Row, Rate, Button, notification } from "antd";
import list_image from "./css/images/list-image.svg";
import "./css/style.css";
import { ShoppingCartOutlined } from "@ant-design/icons";
import { useHistory } from "react-router";
export default function DetailProductItem(props) {
  var { product, quantityCartItem, onAddProductToCart, alreadyInCart } = props;

  var history = useHistory();

  var onAddToCart = () => {
    if (!alreadyInCart) {
      onAddProductToCart(product);
      openNotification("success", "Thêm sản phẩm vào giỏ hàng thành công :D");
    } else openNotification("warning", "Sản phảm đã tồn tại trong giỏ hàng :D");
  };

  var onBuyNow = () => {
    if (!alreadyInCart) {
      onAddProductToCart(product);
      openNotification("success", "Thêm sản phẩm vào giỏ hàng thành công :D");
    } else openNotification("warning", "Sản phảm đã tồn tại trong giỏ hàng :D");
    history.replace("/home/cart");
  };
  return (
    <div>
      <div
        className="cart-info"
        style={{ float: "right", position: "relative", cursor: "pointer" }}
      >
        <ShoppingCartOutlined
          style={{ fontSize: "50px" }}
          onClick={() => {
            history.replace("/home/cart");
          }}
        />
        <span
          className="sl-product"
          style={{
            position: "absolute",
            top: "0px",
            right: "0px",
            borderRadius: "100%",
            backgroundColor: "#F477FF",
            padding: "2px",
            color: "black",
            fontWeight: "bold",
          }}
        >
          {" "}
          &nbsp;{quantityCartItem}&nbsp;{" "}
        </span>
      </div>
      <div className="clear"></div>
      <div className="line"></div>

      <Row gutter={32} className="DeatailProduct" style={{ marginTop: "50px" }}>
        <Col span={9}>
          <div
            className="image"
            style={{ width: "100%", overflow: "hidden", textAlign: "center" }}
          >
            <img src={product.image_big_1} alt="" />
          </div>
          <div
            className="listImage"
            style={{ textAlign: "center", marginTop: "40px" }}
          >
            <img src={list_image} alt="" />
          </div>
        </Col>
        <Col span={12}>
          <div>
            <div className="name">{product.name}</div>
            <div className="decription">
              {product.decription}
              {/* Là một sản phẩm có kích thước màn hình lớn vì vậy Samsung đã trang
              bị cho Galaxy Note 20 Ultra 5G với công nghệ kết nối dữ liệu mạnh
              mẽ cùng thiết kế nguyên khối. Giúp các linh kiện bên trong điện
              thoại được cố định chắc chắn đảm bảo mọi thứ bên trong luôn được
              an toàn. Không những vậy khung nhôm tạo trên những đường viền cực
              kỳ sang trọng và bắt mắt khi nhìn vào. */}
            </div>
            <div className="price">
              {(product.price + "").replace(/\B(?=(\d{3})+(?!\d))/g, ",")} vnđ
            </div>
            <div className="vote">
              <Rate
                disabled
                value={product.rate}
                style={{ fontSize: "50px" }}
              />
            </div>
            <div style={{ marginTop: "20px" }}>
              <Button type="primary" className="cus-button" onClick={onBuyNow}>
                Mua ngay
              </Button>
              <Button
                type="primary"
                className="cus-button"
                style={{
                  backgroundColor: "#00FF19",
                  border: "1px solid #00FF19",
                }}
                onClick={onAddToCart}
              >
                Thêm giỏ hàng
              </Button>
            </div>
          </div>
        </Col>
      </Row>
    </div>
  );
}

const openNotification = (type, description) => {
  notification[type]({
    message: "ADD TO CART",
    description,
  });
};
