import React, { useState } from "react";
import { Col, Row, Rate } from "antd";
import { useHistory } from "react-router";
export default function ProductItem(props) {
  var { product, onChangeRate, onChangeDetailProduct } = props;
  var [rate, setRate] = useState(product.rate);
  var history = useHistory();

  var onDetailProduct = () => {
    onChangeDetailProduct(product);
    history.push("/home/shop/" + product.name);
  };
  var handleChange = (value) => {
    if (rate !== 0) {
      value = parseInt(value);
      setRate(value);
      product.rate = value;
      onChangeRate(product);
    }
  };

  return (
    <Col
      span={12}
      className="ProducItem"
      style={{ backgroundColor: "#FFFFFF" }}
    >
      <Row gutter={32}>
        <Col span={9}>
          <div
            className="image"
            style={{ width: "100%", overflow: "hidden", textAlign: "center" }}
            onClick={onDetailProduct}
          >
            <img src={product.image_small} alt="" />
          </div>
        </Col>
        <Col span={15}>
          <div>
            <div className="name" onClick={onDetailProduct}>
              {product.name}
            </div>
            <div className="price">
              {(product.price + "").replace(/\B(?=(\d{3})+(?!\d))/g, ",")} vnđ
            </div>
            <div className="vote">
              <Rate
                value={product.rate}
                onChange={handleChange}
                style={{ fontSize: "50px" }}
              />
            </div>
          </div>
        </Col>
      </Row>
    </Col>
  );
}
