import React, { useState } from "react";
import { Select } from "antd";
import "./css/style.css";
const { Option } = Select;

export default function SearchPrice(props) {
  var { onChangeFilterSearchPrice } = props;
  var [filterPrice, setFilterPrice] = useState({ start: 0, end: 1000000000 });
  function handleChangeStart(value) {
    setFilterPrice({ ...filterPrice, start: value });
    onChangeFilterSearchPrice({ ...filterPrice, start: value });
  }

  function handleChangeEnd(value) {
    setFilterPrice({ ...filterPrice, end: value });
    onChangeFilterSearchPrice({ ...filterPrice, end: value });
  }

  return (
    <div className="search-price">
      <div style={{ fontWeight: "bold" }}>PRICE: &nbsp;</div>
      <span>&nbsp;&nbsp;&nbsp;Start : </span>
      <Select
        defaultValue={
          (filterPrice.start + "").replace(/\B(?=(\d{3})+(?!\d))/g, " ") +
          " VNĐ"
        }
        style={{ width: 170 }}
        onChange={handleChangeStart}
      >
        <Option value={0}>0 VNĐ</Option>
        <Option value={2000000}>2 000 000 VNĐ</Option>
        <Option value={4000000}>4 000 000 VNĐ</Option>
        <Option value={6000000}>6 000 000 VNĐ</Option>
        <Option value={8000000}>8 000 000 VNĐ</Option>
        <Option value={10000000}>10 000 000 VNĐ</Option>
      </Select>
      <br />
      <span>&nbsp;&nbsp;&nbsp;End: &nbsp; </span>
      <Select
        defaultValue={
          (filterPrice.end + "").replace(/\B(?=(\d{3})+(?!\d))/g, " ") + " VNĐ"
        }
        style={{ width: 170 }}
        onChange={handleChangeEnd}
      >
        <Option value={11000000}>11 000 000 VNĐ</Option>
        <Option value={20000000}>20 000 000 VNĐ</Option>
        <Option value={40000000}>40 000 000 VNĐ</Option>
        <Option value={60000000}>60 000 000 VNĐ</Option>
        <Option value={80000000}>80 000 000 VNĐ</Option>
        <Option value={100000000}>100 000 000 VNĐ</Option>
      </Select>
    </div>
  );
}
