import React from "react";
import { connect } from "react-redux";
import {
  actChangeDetailProduct,
  actDeleteCartItem,
  actSaveCart,
} from "../action/Action";
import Cart from "../component/Cart";
import CartItem from "../component/CartItem";

function CartContainer(props) {
  var { cart, onSaveCart, onDeleteCartItem, onChangeDetailProduct } = props;
  return (
    <Cart
      elemenetCartItem={elemenetCartItem(
        cart,
        onSaveCart,
        onDeleteCartItem,
        onChangeDetailProduct
      )}
      quantityItem={cart.length}
      objPrice={objPrice(cart)}
    />
  );
}

var elemenetCartItem = (
  cart,
  onSaveCart,
  onDeleteCartItem,
  onChangeDetailProduct
) => {
  if (cart.length === 0) return null;
  return cart.map((element, index) => {
    return (
      <CartItem
        key={index}
        cartItem={element}
        onSaveCart={onSaveCart}
        onDeleteCartItem={onDeleteCartItem}
        onChangeDetailProduct={onChangeDetailProduct}
      />
    );
  });
};

var objPrice = (cart) => {
  var subTotal = cart.reduce((sub, element) => {
    return sub + element.quantity * element.product.price;
  }, 0);

  var tax = subTotal * 0.1;
  return {
    subTotal,
    tax,
    total: subTotal + tax,
  };
};
const mapStateToProps = (state) => {
  return {
    cart: state.cart,
  };
};

const mapDispatchToProps = (dispatch, props) => {
  return {
    onSaveCart: (product, quantity) => {
      dispatch(actSaveCart(product, quantity));
    },
    onDeleteCartItem: (product) => {
      dispatch(actDeleteCartItem(product));
    },
    onChangeDetailProduct: (product) => {
      dispatch(actChangeDetailProduct(product));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(CartContainer);
