import React from "react";
import { Row, Col } from "antd";
import "./css/style.css";
import { useHistory } from "react-router-dom";
import logo from "./css/images/logo.svg";
import { Form, Input, Button, Checkbox, Alert, notification} from "antd";
import { UserOutlined, LockOutlined, EyeInvisibleFilled } from "@ant-design/icons";

export default function Login(props) {
  
  var { login, onCheckLogin } = props;
  var history = useHistory();
  if (JSON.parse(localStorage.getItem("tokenlogin"))) {
    console.log("Ahihih");
    openNotification("success","Đăng nhập thành công <3")
    history.push("/home");
  }
  const onFinish = (values) => {
    var account = { username: values.username, password: values.password };
    onCheckLogin(account);
  };

  return (
    <div className="Login">
      <div className="bg-bottom">
        <Row>
          <Col span={10} offset={7}>
            <Row className="logo" style={{ paddingTop: "97px" }}>
              <img src={logo} alt="error" />
            </Row>
            <Row className="form-login">
              <Form
                name="normal_login"
                className="login-form"
                initialValues={{ remember: true }}
                onFinish={onFinish}
              >
                {login === false ? (
                  <Alert
                    message="Tài khoản or mật khẩu không chính xác"
                    type="warning"
                    showIcon
                  />
                ) : null}

                <Form.Item name="username">
                  <Input
                    prefix={<UserOutlined className="site-form-item-icon" />}
                    placeholder="Username"
                  />
                </Form.Item>
                
                <Form.Item name="password" className="input-pass">
                  <Input
                    prefix={<LockOutlined className="site-form-item-icon" />}
                    type="password"
                    placeholder="Password"
                    suffix={<EyeInvisibleFilled className='cus-eye-pass'/>}
                  />
                </Form.Item>
                <Form.Item>
                  <Form.Item name="remember" valuePropName="checked" noStyle>
                    <Checkbox style={{ color: "white", fontWeight: "normal" }}>
                      Remember me
                    </Checkbox>
                  </Form.Item>

                  <a
                    className="login-form-forgot"
                    href="/"
                    style={{ color: "white" }}
                  >
                    Forgot password
                  </a>
                </Form.Item>

                <Form.Item>
                  <Button
                    type="primary"
                    htmlType="submit"
                    className="login-form-button"
                  >
                    Log in
                  </Button>
                </Form.Item>

                <Row
                  className="info-footter"
                  style={{
                    textAlign: "center",
                    color: "#99D7F7",
                    fontSize: "12px;",
                    fontFamily: "Roboto",
                  }}
                >
                  <div style={{ marginTop: "30px" }}>
                    Nếu bạn có thắc mắc hay cần giải đáp, vui lòng liên hệ số
                    điện thoại:{" "}
                    <a
                      href="/"
                      style={{
                        textDecoration: "underline",
                        fontWeight: "bold",
                        color: "#99D7F7",
                      }}
                    >
                      19001000
                    </a>
                  </div>
                  <div style={{ width: "100%", marginTop: "7px" }}>
                    Bản quyền thuộc về AnyBim
                  </div>
                </Row>
              </Form>
            </Row>
          </Col>
        </Row>
      </div>
    </div>
  );
}

const openNotification = (type, description) => {
  notification[type]({
    message: "SYSTEM",
    description,
  });
};
