import * as types from "../constant/ActionType";
var data = JSON.parse(localStorage.getItem("tokenlogin"));
var initinalState = data
  ? { check: data }
  : {
      check: null,
    };

export var login = (state = initinalState, action) => {
  switch (action.type) {
    case types.CHECK_LOGIN:
      var { account } = action;
      state = false;
      if (account.username === "admin" && account.password === "123") {
        localStorage.setItem("tokenlogin", JSON.stringify(true));
        state = true;
        return state;
      }
      return state;
    default:
      return state;
  }
};
