import React from "react";
import { Row, Col, notification } from "antd";
import img from "./css/images/img-pro.svg";
import img_remove_cart_item from "./css/images/remove_cart.svg";
import { useHistory } from "react-router";
export default function CartItem(props) {
  var { cartItem, onSaveCart, onDeleteCartItem, onChangeDetailProduct } = props;
  var history = useHistory();
  var onViewDetailProduct = () => {
    onChangeDetailProduct(cartItem.product);
    history.replace("/home/shop/" + cartItem.product.name);
  };
  return (
    <div
      style={{
        borderBottom: "1px solid #D5D5D5",
        paddingBottom: "20px",
        position: "relative",
      }}
    >
      <Col span={24}>
        <Row gutter={12} className="CartItem" style={{ marginTop: "50px" }}>
          <Col span={5}>
            <div
              className="image"
              style={{
                width: "100%",
                overflow: "hidden",
                textAlign: "center",
                cursor: "pointer",
              }}
              onClick={onViewDetailProduct}
            >
              <img src={cartItem.product.image_small} alt="" />
            </div>
          </Col>
          <Col span={15}>
            <div>
              <div
                className="name"
                style={{ cursor: "pointer" }}
                onClick={onViewDetailProduct}
              >
                {" "}
                {cartItem.product.name}
              </div>
              <div className="decription">{cartItem.product.decription}</div>
              <div className="price">
                {(cartItem.product.price + "").replace(
                  /\B(?=(\d{3})+(?!\d))/g,
                  ","
                )}{" "}
                VNĐ
              </div>
            </div>
          </Col>
          <Col
            span={4}
            style={{
              textAlign: "center",
              fontWeight: "bold",
              fontSize: "24px",
              lineHeight: "250px",
            }}
          >
            <span
              style={{ cursor: "pointer" }}
              onClick={() => {
                onSaveCart(cartItem.product, 1);
                openNotification("success", "Cập nhập thành công ;)");
              }}
            >
              +
            </span>{" "}
            &nbsp;&nbsp;&nbsp;{cartItem.quantity}
            &nbsp;&nbsp;&nbsp;
            <span
              style={{ cursor: "pointer" }}
              onClick={() => {
                onSaveCart(cartItem.product, -1);
                if (cartItem.quantity === 1)
                  openNotification("warning", 
                    "Số lượng tối thiểu là 1 đơn vị sản phẩm :D"
                  );
                else openNotification("success","Cập nhập thành công ;)");
              }}
            >
              {" "}
              -
            </span>
          </Col>
        </Row>

        <div
          className="remove-cart-item"
          style={{
            display: "inline-block",
            position: "absolute",
            right: "-16px",
            top: "-63px",
          }}
        >
          <img
            src={img_remove_cart_item}
            alt=""
            style={{
              cursor: "pointer",
              padding: "3px",
              border: "1px solid black",
              borderRadius: "100%",
              display: "inline-block",
            }}
            onClick={() => {
              onDeleteCartItem(cartItem.product);
              openNotification("success", "Xóa thành công :D");
            }}
          />
        </div>
      </Col>
    </div>
  );
}

const openNotification = (type, description) => {
  notification[type]({
    message: "UPDATE TO CART",
    description,
  });
};
