import * as types from "../constant/ActionType";
var data = JSON.parse(localStorage.getItem("cart-mobile"));
var initinalState = data ? data : [];

var findIndex = (products, id_product) => {
  var index = -1;
  for (var i = 0; i < products.length; i++)
    if (products[i].product.id === id_product) {
      index = i;
      break;
    }
  return index;
};

export var cart = (state = initinalState, action) => {
  var index = -1;
  switch (action.type) {
    case types.SAVE_CART:
      var cartItem = {
        product: action.product,
        quantity: action.quantity,
      };
      if (state.length === 0) state.push(cartItem);
      else {
        index = findIndex(state, action.product.id);

        if (index === -1) state.push(cartItem);
        else {
          if (state[index].quantity === 1 && action.quantity === -1)
            state[index] = {
              product: action.product,
              quantity: 1,
            };
          else
            state[index] = {
              product: action.product,
              quantity: state[index].quantity + action.quantity,
            };
        }
      }

      localStorage.setItem("cart-mobile", JSON.stringify(state));
      return [...state];
    case types.DELETE_CART_ITEM:
      index = findIndex(state, action.product.id);
      if (index !== -1) state.splice(index, 1);
      localStorage.setItem("cart-mobile", JSON.stringify(state));
      return [...state];
    case types.ADD_PRODUCT_TO_CART:
      index = findIndex(state, action.product.id);
      if (index === -1) {
        state.push({
          product: action.product,
          quantity: 1,
        });
        localStorage.setItem("cart-mobile", JSON.stringify(state));
      }
      return [...state];
    default:
      return [...state];
  }
};
