var findIndex = (products, id_product) => {
  var index = -1;
  for (var i = 0; i < products.length; i++)
    if (products[i].id === id_product) {
      index = i;
      break;
    }
  return index;
};

export var products = (state = initinalState, action) => {
  var index = -1;
  switch (action.type) {
    case types.CHANGE_RATE:
      index = findIndex(state, action.product.id);
      state[index] = action.product;
      localStorage.setItem("products", JSON.stringify(state));
      return [...state];
    default:
      return [...state];
  }
};
