import * as types from "../constant/ActionType";

var initinalState = {
  id: "",
  name: "",
  image: "",
  decription: "",
  price: 0,
  rate: 0,
};

export var detailProduct = (state = initinalState, action) => {
  switch (action.type) {
    case types.CHANGE_DETAIL_PRODUCT:
      state = action.product;
      return {...state};
    default:
      return {...state};
  }
};
