import React, { useState } from "react";
import { Select } from "antd";
import "./css/style.css";
const { Option } = Select;
export default function SearchRate(props) {
  var { onChangeFilterSearchRate } = props;

  var [filterRate, setFilterRate] = useState({ start: 1, end: 5 });
  function handleChangeStart(value) {
    setFilterRate({ ...filterRate, start: value });
    onChangeFilterSearchRate({ ...filterRate, start: value });
  }

  function handleChangeEnd(value) {
    setFilterRate({ ...filterRate, end: value });
    onChangeFilterSearchRate({ ...filterRate, end: value });
  }
  return (
    <div className="search-rate">
      <div style={{ fontWeight: "bold" }}>RATE: &nbsp;&nbsp;</div>
      <span>&nbsp;&nbsp;&nbsp;Start : </span>
      <Select
        defaultValue={filterRate.start}
        style={{ width: 80 }}
        onChange={handleChangeStart}
      >
        <Option value={1}>1 sao</Option>
        <Option value={2}>2 sao</Option>
        <Option value={3}>3 sao</Option>
        <Option value={4}>4 sao</Option>
        <Option value={5}>5 sao</Option>
      </Select>

      <span>&nbsp;&nbsp;&nbsp;End : </span>
      <Select
        defaultValue={filterRate.end}
        style={{ width: 80 }}
        onChange={handleChangeEnd}
      >
        <Option value={1}>1 sao</Option>
        <Option value={2}>2 sao</Option>
        <Option value={3}>3 sao</Option>
        <Option value={4}>4 sao</Option>
        <Option value={5}>5 sao</Option>
      </Select>
    </div>
  );
}
