import * as types from "../constant/ActionType";

var initinalState = {
  name: "",
  rate: {
    start: 1,
    end: 5,
  },
  price: {
    start: 0,
    end: 100000000,
  },
};

export var filterSearch = (state = initinalState, action) => {
  switch (action.type) {
    case types.CHANGE_FILTER_SEARCH_NAME:
      state = { ...state, name: action.filterName };
      return { ...state };
    case types.CHANGE_FILTER_SEARCH_RATE:
      state = { ...state, rate: action.filterRate };
      return { ...state };
    case types.CHANGE_FILTER_SEARCH_PRICE:
      state = { ...state, price: action.filterPrice };
      return { ...state };
    default:
      return { ...state };
  }
};
