
import Home from "../component/Home";
import PageNotFound from "../component/PageNotFound";
import LoginContainer from "../container/LoginContainer";

export const routes = [
  {
    path: "/",
    exact: true,
    main: () => <LoginContainer />,
  },
  {
    path: "/login",
    exact: true,
    main: () => <LoginContainer />,
  },
  {
    path: "/home",
    exact: false,
    main: () => <Home />,
  },
  {
    path: "", 
    exact: false,
    main: () => <PageNotFound />,
  },
];
