import React from "react";
import { connect } from "react-redux";
import { actChangeDetailProduct, actChangeRate } from "../action/Action";
import ProductItem from "../component/ProductItem";
import Products from "../component/Products";

function ProductsContainer(props) {
  var { products, onChangeRate, onChangeDetailProduct, filterSearch } = props;
  return (
    <Products
      elemenetProductItem={elemenetProductItem(
        products,
        onChangeRate,
        onChangeDetailProduct,
        filterSearch
      )}
    />
  );
}

var elemenetProductItem = (
  products,
  onChangeRate,
  onChangeDetailProduct,
  filterSearch
) => {
  if (products.length === 0) return <h1>Không tồn tại sản phẩm nào cả</h1>;
  var filterName = filterSearch.name;
  if (filterName !== "") {
    products = products.filter((element) => {
      return (
        element.name.toLowerCase().indexOf(filterName.toLowerCase()) !== -1
      );
    });
  }

  var { rate } = filterSearch;
  if (rate.start <= rate.end) {
    products = products.filter((element) => {
      return element.rate >= rate.start && element.rate <= rate.end;
    });
  } else products = [];

  var { price } = filterSearch;
  if (price.start <= price.end) {
    products = products.filter((element) => {
      return element.price >= price.start && element.price <= price.end;
    });
  } else products = [];

  return products.map((element, index) => {
    return (
      <ProductItem
        key={index}
        product={element}
        onChangeRate={onChangeRate}
        onChangeDetailProduct={onChangeDetailProduct}
      />
    );
  });
};

const mapStateToProps = (state) => {
  return {
    products: state.products,
    filterSearch: state.filterSearch,
  };
};

const mapDispatchToProps = (dispatch, props) => {
  return {
    onChangeRate: (product) => {
      dispatch(actChangeRate(product));
    },
    onChangeDetailProduct: (product) => {
      dispatch(actChangeDetailProduct(product));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ProductsContainer);
