import React, { useState } from "react";
import { Input } from "antd";
import { SearchOutlined } from "@ant-design/icons";
import "./css/style.css";
import SearchRate from "./SearchRate";
import SearchPrice from "./SearchPrice";

export default function SearchProduct(props) {
  var {
    onChangeFilterSearchName,
    onChangeFilterSearchRate,
    onChangeFilterSearchPrice,
  } = props;
  var [filterName, setFilterName] = useState("");
  var onChange = (event) => {
    setFilterName(event.target.value);
    onChangeFilterSearchName(event.target.value);
  };

  return (
    <div className="search_name">
      <Input
        size="large"
        placeholder="Input content"
        suffix={<SearchOutlined />}
        value={filterName}
        onChange={onChange}
        style={{ marginBottom: "10px" }}
      />
      <SearchRate onChangeFilterSearchRate={onChangeFilterSearchRate} />
      <SearchPrice onChangeFilterSearchPrice={onChangeFilterSearchPrice} />
    </div>
  );
}
