import React from "react";
import { Layout, Button } from "antd";
import LogoHome from "./css/images/LogoHome.svg";
import { NavLink, useHistory } from "react-router-dom";
import userLogo from "./css/images/user.svg";
const { Header } = Layout;
export default function HeaderHome() {
  var history = useHistory();
  var onLogOut = () => {
    localStorage.removeItem("tokenlogin");
    history.replace("/login");
  };
  return (
    <Header style={{ height: "120px", backgroundColor: "#C6E5F4" }}>
      <span
        className="logo"
        style={{ display: "inline-block", height: "100%", lineHeight: "120px" }}
      >
        <NavLink to="/home">
          <img src={LogoHome} alt="" style={{ verticalAlign: "middle" }} />
        </NavLink>
      </span>
      <span className="name-website">
        <h1
          style={{
            position: "relative",
            display: "inline-block",
            top: "10px",
            marginLeft: "25px",
          }}
        >
          Mobile Shopping
        </h1>
      </span>

      <div
        className="user-logo"
        style={{ float: "right", lineHeight: "120px", cursor: "pointer" }}
      >
        <NavLink to="/home/myprofile">
          <img src={userLogo} alt="" />
        </NavLink>
        <Button
          type="warning"
          danger
          style={{ backgroundColor: "red", color: "white", marginLeft: "15px" }}
          onClick={onLogOut}
        >
          Đăng xuất
        </Button>
      </div>
      <div className="clear"></div>
    </Header>
  );
}
