import React from "react";
import { Layout, Breadcrumb } from "antd";
import { NavLink, Route, Switch, useLocation } from "react-router-dom";
import Shop from "./Shop";
import MyProfile from "./MyProfile";
import DetailProductItemContainer from "../container/DetailProductItemContainer";
import CartContainer from "../container/CartContainer";
// import PageNotFound from "./PageNotFound";
const { Content } = Layout;
export default function ContentHome() {
  var location = useLocation();
  var links = location.pathname.split("/");
  links.splice(0, 1);

  return (
    <Content>
      <h1 style={{ marginLeft: "21px" }}>
        {formatCaitalize(links[links.length - 1])}
      </h1>
      <Layout style={{ padding: "0 24px 24px" }}>
        <Breadcrumb style={{ width: "400px" }}>
          {showBreadcrumb(links)}
        </Breadcrumb>
        <Content
          className="site-layout-background custom-content"
          style={{
            padding: 24,
            margin: 0,
            minHeight: 280,
          }}
        >
          <Switch>
            <Route path="/home/cart" exact component={CartContainer}></Route>
            <Route path="/home/shop" exact component={Shop}></Route>
            <Route path="/home/myprofile" exact component={MyProfile}></Route>
            <Route path="/home/shop/:name" exact component={DetailProductItemContainer}></Route>
            {/* <Route path="" exact component={PageNotFound}></Route> */}
            
          </Switch>
        </Content>
      </Layout>
    </Content>
  );
}

var formatCaitalize = (str) => {
  return str[0].toUpperCase() + str.slice(1);
};

var showBreadcrumb = (links) => {
  var arr = [];
  var link = "";
  if (links.length > 1) {
    for (var element of links) {
      link += '/' + element;
      arr.push(
        <Breadcrumb.Item>
          <NavLink to={link}>{formatCaitalize(element)}</NavLink>
        </Breadcrumb.Item>
      );
    }
  }
  arr[arr.length - 1] = (
    <Breadcrumb.Item>
      {formatCaitalize(links[links.length - 1])}
    </Breadcrumb.Item>
  );
  return arr;
};
