import React from "react";
import { Layout, Menu } from "antd";
import {
  MenuOutlined,
  ShopOutlined,
  ShoppingCartOutlined,
  UserOutlined,
} from "@ant-design/icons";
import "./css/style.css";
import { NavLink} from "react-router-dom";
const { SubMenu } = Menu;
const { Sider } = Layout;
export default function SliderHome() {
  return (
    <Sider width={200} className="site-layout-background menu-cus">
      <Menu
        mode="inline"
        defaultOpenKeys={["sub1"]}
        style={{ minHeight: "100%", borderRight: 0, border: "1px solid black" }}
        expandIcon={<MenuOutlined />}
      >
        <SubMenu key="sub1" title="Menu">
          <Menu.Item key="1">
            <NavLink
              to="/home/shop"
              className="cus-menu-item"
              activeClassName="onActive"
            >
              <ShopOutlined style={{ marginRight: "10px" }} />
              Shop
            </NavLink>
          </Menu.Item>

          <Menu.Item key="2">
            <NavLink
              to="/home/cart"
              className="cus-menu-item"
              activeClassName="onActive"
            >
              <ShoppingCartOutlined style={{ marginRight: "10px" }} />
              Cart
            </NavLink>
          </Menu.Item>

          <Menu.Item key="3">
            <NavLink
              to="/home/myprofile"
              className="cus-menu-item"
              activeClassName="onActive"
            >
              <UserOutlined style={{ marginRight: "10px" }} />
              My Profile
            </NavLink>
          </Menu.Item>
        </SubMenu>
      </Menu>
    </Sider>
  );
}
