import React from "react";
import { connect } from "react-redux";
import {
  actChangeFilterSearchName,
  actChangeFilterSearchPrice,
  actChangeFilterSearchRate,
} from "../action/Action";
import SearchProduct from "../component/SearchProduct";

function SearchProductContainer(props) {
  var {
    onChangeFilterSearchName,
    onChangeFilterSearchRate,
    onChangeFilterSearchPrice,
  } = props;
  return (
    <SearchProduct
      onChangeFilterSearchName={onChangeFilterSearchName}
      onChangeFilterSearchRate={onChangeFilterSearchRate}
      onChangeFilterSearchPrice={onChangeFilterSearchPrice}
    />
  );
}

var mapDispatchToProps = (dispatch) => {
  return {
    onChangeFilterSearchName: (filterName) => {
      dispatch(actChangeFilterSearchName(filterName));
    },
    onChangeFilterSearchRate: (filterRate) => {
      dispatch(actChangeFilterSearchRate(filterRate));
    },
    onChangeFilterSearchPrice: (filterPrice) => {
      dispatch(actChangeFilterSearchPrice(filterPrice));
    },
  };
};

export default connect(null, mapDispatchToProps)(SearchProductContainer);
