import React from "react";
import { Row, Col } from "antd";

export default function Cart(props) {
  var { elemenetCartItem, quantityItem, objPrice } = props;
  return (
    <div>
      <div
        className="view-quantity"
        style={{
          float: "right",
          position: "relative",
          top: "-10px",
          fontSize: "24px",
        }}
      >
        {quantityItem} items in bag
      </div>

      <div className="clear"></div>
      <div className="line"></div>
      <Row>{elemenetCartItem}</Row>

      <Row style={{ marginTop: "40px" }}>
        <Col span={5} />
        <Col
          span={13}
          style={{
            textAlign: "right",
            fontFamily: "Robato",
            fontSize: "24px",
            fontWeight: "bold",
          }}
        >
          <div className="mg-bt">Sub Total</div>
          <div className="mg-bt">Tax</div>
          <div className="mg-bt">Total</div>
        </Col>
        <Col
          span={6}
          style={{
            textAlign: "center",
            fontFamily: "Robato",
            fontSize: "24px",
            fontWeight: "bold",
            
          }}
        >
          <div className="mg-bt subtotal">
            {(objPrice.subTotal + "").replace(/\B(?=(\d{3})+(?!\d))/g, ",")} VNĐ
          </div>
          <div className="mg-bt tax">
            {(objPrice.tax + "").replace(/\B(?=(\d{3})+(?!\d))/g, ",")} VNĐ
          </div>
          <div className="mg-bt total">
            {(objPrice.total + "").replace(/\B(?=(\d{3})+(?!\d))/g, ",")} VNĐ
          </div>
        </Col>
      </Row>
    </div>
  );
}
