import React from "react";
import ProductsContainer from "../container/ProductsContainer";
import SearchProductContainer from "../container/SearchProductContainer";

export default function Shop() {
  return (
    <div className="Shop" style={{marginBottom: "50px"}}>
      <SearchProductContainer style={{ width: "200px" }} />
      <div className="clear"></div>
      <div className="line"></div>
      <ProductsContainer/>
    </div>
  );
}
