import * as types from "../constant/ActionType";
export const actCheckLogin = (account) => {
  return {
    type: types.CHECK_LOGIN,
    account,
  };
};

export const actChangeRate = (product) => {
  return {
    type: types.CHANGE_RATE,
    product,
  };
};

export const actChangeDetailProduct = (product) => {
  return {
    type: types.CHANGE_DETAIL_PRODUCT,
    product,
  };
};

export const actSaveCart = (product, quantity) => {
  return {
    type: types.SAVE_CART,
    product,
    quantity,
  };
};

export const actDeleteCartItem = (product) => {
  return {
    type: types.DELETE_CART_ITEM,
    product,
  };
};

export const actAddProductToCart = (product) => {
  return {
    type: types.ADD_PRODUCT_TO_CART,
    product,
  };
};

export const actChangeFilterSearchName = (filterName) => {
  return {
    type: types.CHANGE_FILTER_SEARCH_NAME,
    filterName,
  };
};

export const actChangeFilterSearchRate = (filterRate) => {
  return {
    type: types.CHANGE_FILTER_SEARCH_RATE,
    filterRate,
  };
};

export const actChangeFilterSearchPrice = (filterPrice) => {
  return {
    type: types.CHANGE_FILTER_SEARCH_PRICE,
    filterPrice,
  };
};
