import React from "react";
import { connect } from "react-redux";
import { actAddProductToCart } from "../action/Action";
import DetailProductItem from "../component/DetailProductItem";

var findIndex = (products, id_product) => {
  var index = -1;
  for (var i = 0; i < products.length; i++)
    if (products[i].product.id === id_product) {
      index = i;
      break;
    }
  return index;
};

function DetailProductItemContainer(props) {
  var { product, cart, onAddProductToCart } = props;
  var index = findIndex(cart, product.id);
  var alreadyInCart = index!==-1?true:false;

  return (
    <DetailProductItem
      product={product}
      onAddProductToCart={onAddProductToCart}
      quantityCartItem={cart.length}
      alreadyInCart={alreadyInCart}
    />
  );
}




const mapStateToProps = (state) => {
  return {
    product: state.detailProduct,
    cart: state.cart,
  };
};

const mapDispatchToProps = (dispatch, props) => {
  return {
    onAddProductToCart: (product) => {
      dispatch(actAddProductToCart(product));
    },
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(DetailProductItemContainer);


